import { stopSubmit } from 'redux-form';
import { authAPI } from '../API/API';

const SET_USER_DATA = 'SET_USER_DATA';
const GET_CAPTCHA_URL = 'GET_CAPTCHA_URL';


const initialState = {
  id: null as number | null,
  login: null as string | null,
  email: null as string | null,
  isAuth: false as boolean | null,
  url: null as string | null,
};
type initialStateType = typeof initialState

const authReducer = (state = initialState, action: any): initialStateType => {
  switch (action.type) {
    case SET_USER_DATA:
      return {
        ...state,
        ...action.data,
      };
    case GET_CAPTCHA_URL:
      return {
        ...state,
        url: action.url,
      };

    default:
      return state;
  }
};

export const setAuthUserData = (id: number | null, login: string | null, email: string | null, isAuth: boolean): setAuthUserDataType => ({
  type: SET_USER_DATA,
  data: {
    id, login, email, isAuth,
  },
});
type setAuthUserDataDataType = {id: number | null, login: string | null, email: string | null, isAuth: boolean}
type setAuthUserDataType = {type: typeof SET_USER_DATA, data: setAuthUserDataDataType}
export const putCaptchaUrl = (url: string) => ({ type: GET_CAPTCHA_URL, url });

export const signIn = () => async (dispatch: any) => {
  const response = await authAPI.authMe();
  if (response.data.resultCode === 0) {
    const { id, login, email } = response.data.data;
    dispatch(setAuthUserData(id, login, email, true));
  }
};

export const getCaptchaUrl = () => async (dispatch: any) => {
  const response = await authAPI.getCaptcha();
  dispatch(putCaptchaUrl(response.data.url));
};

export const logIn = (email: string, password: string, rememberMe: boolean, captcha: boolean) => async (dispatch: any) => {
  const response = await authAPI.login(email, password, rememberMe, captcha);
  if (response.data.resultCode === 0) {
    dispatch(signIn());
  } else {
    if (response.data.resultCode === 10) {
      dispatch(getCaptchaUrl());
    }
    const errorMessage = response.data.messages.length > 0 ? response.data.messages[0] : 'Some error';
    dispatch(stopSubmit('auth', { _error: errorMessage }));
  }
};

export const logout = () => async (dispatch: any) => {
  const response = await authAPI.logout();
  if (response.data.resultCode === 0) {
    dispatch(setAuthUserData(null, null, null, false));
  }
};


export default authReducer;
