import { stopSubmit } from 'redux-form';
import { profileAPI } from '../API/API';
import { photosType, postType, profileType } from './reducerTypes';

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const GET_STATUS = 'SET_STATUS';
const UPDATE_USER_PHOTO = 'UPDATE_USER_PHOTO';
const SET_STATUS_ERROR = 'SET_STATUS_ERROR';
const initialState = {
  posts: [
    { id: 1, message: 'how are you?', likesCount: 12 },
    { id: 2, message: 'Its my first post', likesCount: 15 },
    { id: 3, message: 'How are you?', likesCount: 10 },
    { id: 4, message: 'Whats`s new?', likesCount: 11 },
    { id: 5, message: 'Whats up?', likesCount: 17 },
    { id: 6, message: 'Good morning', likesCount: 16 },
  ] as Array<postType>,
  profile: null as profileType | null,
  status: '',
  statusError: null as string | null,
};
type initialStateType = typeof initialState

const profileReducer = (state = initialState, action: any): initialStateType => {
  switch (action.type) {
    case ADD_POST:
      return {
        ...state,
        posts: [...state.posts, { id: 5, message: action.post, likesCount: 0 }],
      };
    case SET_USER_PROFILE:
      return {
        ...state,
        profile: action.profile,
      };
    case GET_STATUS:
      return {
        ...state,
        status: action.status,
      };
    case SET_STATUS_ERROR:
      return {
        ...state,
        statusError: action.error,
      };
    case UPDATE_USER_PHOTO:
      return {
        ...state,
        profile: { ...state.profile, photos: action.photos } as profileType,
      };
    default:
      return state;
  }
};

export const addPost = (post: postType) => ({ type: ADD_POST, post });

export const setUserProfile = (profile: profileType): setUserProfileType => ({ type: SET_USER_PROFILE, profile });
type setUserProfileType = {type: typeof SET_USER_PROFILE, profile: profileType}
export const setStatus = (status: string): setStatusType => ({ type: GET_STATUS, status });
type setStatusType = {type: typeof GET_STATUS, status: string}
export const updateUserPhoto = (photos: photosType): updateUserPhotoType => ({ type: UPDATE_USER_PHOTO, photos });
type updateUserPhotoType = {type: typeof UPDATE_USER_PHOTO, photos: photosType}
export const setStatusError = (error: string | null): setStatusErrorType => ({ type: SET_STATUS_ERROR, error });
type setStatusErrorType ={type: typeof SET_STATUS_ERROR, error: string | null}

export const getProfile = (userID: number) => async (dispatch: any) => {
  const response = await profileAPI.getUserProfile(userID);
  dispatch(setUserProfile(response.data));
};
export const getStatus = (userID: number) => async (dispatch: any) => {
  const response = await profileAPI.getStatus(userID);
  dispatch(setStatus(response.data));
};

export const updateStatus = (status: string) => async (dispatch: any) => {
  const response = await profileAPI.updateStatus(status);
  if (response.data.resultCode === 0) {
    dispatch(setStatus(status));
    dispatch(setStatusError(null));
  } else if (response.data.resultCode === 1) {
    const error = response.data.messages[0];
    dispatch(setStatusError(error));
  }
};
export const updateUserInformation = (userData: profileType) => async (dispatch: any, getState: any) => {
  const response = await profileAPI.updateUserProfileInformation(userData);
  const userID = getState().auth.id;
  if (response.data.resultCode === 0) {
    dispatch(getProfile(userID));
  } else {
    dispatch(stopSubmit('contacts', { _error: response.data.messages[0] }));
  }
};

export const saveUserPhoto = (photo: any) => async (dispatch: any) => {
  const response = await profileAPI.savePhoto(photo);
  if (response.data.resultCode === 0) {
    dispatch(updateUserPhoto(response.data.data.photos));
  }
};

export default profileReducer;
