import { dialogsAPI } from '../API/API';
import {dialogType, messageType, newMessageType} from './reducerTypes';

const GET_DIALOGS = 'GET_DIALOGS';
const UPLOAD_DIALOG = 'UPLOAD_DIALOG';
const IS_FETCHING = 'ON_FETCHING';
const SEND_MESSAGE = 'SEND_MESSAGE';

const initialState = {
  dialogs: [] as Array<dialogType>,
  messages: [] as Array<messageType>,
  isFetching: false,
};

type initialStateType = typeof initialState

const dialogsReducer = (state = initialState, action: any): initialStateType => {
  switch (action.type) {
    case GET_DIALOGS:
      return {
        ...state,
        dialogs: [...state.dialogs, ...action.dialogs],
      };
    case UPLOAD_DIALOG:
      return {
        ...state,
        messages: action.messages.items,
      };
    case IS_FETCHING:
      return {
        ...state, isFetching: action.boolean,
      };
    case SEND_MESSAGE:
      return {
        ...state,
        messages: [...state.messages, action.message],
      };
    default:
      return state;
  }
};

export const getUserDialogs = (dialogs: Array<dialogType>): getUserDialogsType => ({ type: GET_DIALOGS, dialogs });
type getUserDialogsType = {type: typeof GET_DIALOGS, dialogs: Array<dialogType>}
export const setDialog = (messages: Array<messageType>): setDialogType => ({ type: UPLOAD_DIALOG, messages });
type setDialogType = {type: typeof UPLOAD_DIALOG, messages: Array<messageType>}
export const isFetching = (boolean: boolean): isFetchingType => ({ type: IS_FETCHING, boolean });
type isFetchingType = {type: typeof IS_FETCHING, boolean: boolean}
export const sendMessageAC = (message: newMessageType): sendMessageACType => ({ type: SEND_MESSAGE, message });
type sendMessageACType = {type: typeof SEND_MESSAGE, message: newMessageType}


export const getDialogs = () => async (dispatch: any) => {
  const response = await dialogsAPI.getDialogs();
  dispatch(isFetching(true));
  dispatch(getUserDialogs(response.data));
};

export const getExactDialog = (id: number) => async (dispatch: any) => {
  dispatch(isFetching(false));
  const response = await dialogsAPI.geExactDialog(id);
  await dispatch(setDialog(response.data));
  dispatch(isFetching(true));
};
export const startChatting = (id: number) => async (dispatch: any) => { // непонятно что диспатчить
  const response = await dialogsAPI.startChatting(id);
};

export const updateViewedStatus = (messageId: string) => async (dispatch: any) => { // Получаем true/false, пока не понятно куда диспатчить
  const response = await dialogsAPI.viewedMessage(messageId);
};


export const sendMessage = (userId: number, message: string) => async (dispatch: any) => {
  const response = await dialogsAPI.sendMessage(userId, message);
  if (response.data.resultCode === 0) {
    dispatch(sendMessageAC(response.data.data.message));
    dispatch(startChatting(userId));
  }
};


export default dialogsReducer;
