import { usersAPI } from '../API/API';
import { userType } from './reducerTypes';

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const SET_CURRENT_PAGE = 'SET_CURRENT_PAGE';
const SET_TOTAL_USERS_COUNT = 'SET_TOTAL_USERS_COUNT';
const IS_TOGGLE_FETCHING = 'IS_TOGGLE_FETCHING';
const TOGGLE_IS_FOLLOWING_PROGRESS = 'TOGGLE_IS_FOLLOWING_PROGRESS';


const initialState = {
  users: [] as Array<userType>,
  pageSize: 5,
  totalUsersCount: 0,
  currentPage: 1,
  isFetching: false,
  followingInProgress: [] as Array<number>,
};
type initialStateType = typeof initialState

const usersReducer = (state = initialState, action: any): initialStateType => {
  switch (action.type) {
    case FOLLOW:
      return {
        ...state,
        users: state.users.map((user) => {
          if (user.id === action.userID) {
            return { ...user, followed: true };
          }
          return user;
        }),
      };
    case UNFOLLOW:
      return {
        ...state,
        users: state.users.map((user) => {
          if (user.id === action.userID) {
            return { ...user, followed: false };
          }
          return user;
        }),
      };
    case SET_USERS:
      return {
        ...state, users: action.users,
      };
    case SET_CURRENT_PAGE:
      return {
        ...state, currentPage: action.currentPage,
      };
    case SET_TOTAL_USERS_COUNT:
      return {
        ...state, totalUsersCount: action.count,
      };
    case IS_TOGGLE_FETCHING:
      return {
        ...state, isFetching: action.isFetching,
      };
    case TOGGLE_IS_FOLLOWING_PROGRESS:
      return {
        ...state,
        followingInProgress: action.isFetching
          ? [...state.followingInProgress, action.userID]
          : state.followingInProgress.filter((id) => id !== action.userID),
      };

    default:
      return state;
  }
};

export const followAC = (userID: number): followACType => (
  { type: FOLLOW, userID });
type followACType = {type: typeof FOLLOW, userID: number }
export const unfollowAC = (userID: number): unfollowACType => (
  { type: UNFOLLOW, userID });
type unfollowACType = {type: typeof UNFOLLOW, userID: number }

export const setUsers = (users: Array<userType>): setUsersType => (
  { type: SET_USERS, users });
type setUsersType = {type: typeof SET_USERS, users: Array<userType> }

export const setCurrentPage = (currentPage:number): setCurrentPageType => (
  { type: SET_CURRENT_PAGE, currentPage });
type setCurrentPageType = {type: typeof SET_CURRENT_PAGE, currentPage: number }

export const setTotalUsersCount = (totalCount: number): setTotalUsersCountType => (
  { type: SET_TOTAL_USERS_COUNT, count: totalCount });
type setTotalUsersCountType = {type: typeof SET_TOTAL_USERS_COUNT, count: number }

export const isToggleFetching = (isFetching: boolean): isToggleFetchingType => (
  { type: IS_TOGGLE_FETCHING, isFetching });
type isToggleFetchingType = {type: typeof IS_TOGGLE_FETCHING, isFetching: boolean }

export const toggleFollowingProgress = (isFetching: boolean, userID: number): toggleFollowingProgressType => (
  { type: TOGGLE_IS_FOLLOWING_PROGRESS, isFetching, userID });
type toggleFollowingProgressType = {type: typeof TOGGLE_IS_FOLLOWING_PROGRESS, isFetching: boolean, userID: number }


export const getUsers = (currentPage: number, pageSize: number) => async (dispatch: any) => {
  dispatch(isToggleFetching(true));
  const data = await usersAPI.getUsers(currentPage, pageSize);
  dispatch(isToggleFetching(false));
  dispatch(setUsers(data.items));
  dispatch(setTotalUsersCount(data.totalCount));
};

export const changePage = (currentPage: number, pageSize: number) => async (dispatch: any) => {
  try {
    dispatch(setCurrentPage(currentPage));
    dispatch(isToggleFetching(true));
    const data = await usersAPI.getUsers(currentPage, pageSize);
    dispatch(isToggleFetching(false));
    dispatch(setUsers(data.items));
  } catch (e) {
    alert(e);
  }
};

export const follow = (userID: number) => async (dispatch: any) => {
  dispatch(toggleFollowingProgress(true, userID));
  const response = await usersAPI.follow(userID);
  if (response.data.resultCode === 0) {
    dispatch(followAC(userID));
  }
  dispatch(toggleFollowingProgress(false, userID));
};
export const unfollow = (userID: number) => async (dispatch: any) => {
  dispatch(toggleFollowingProgress(true, userID));
  const response = await usersAPI.unfollow(userID);
  if (response.data.resultCode === 0) {
    dispatch(unfollowAC(userID));
  }
  dispatch(toggleFollowingProgress(false, userID));
};


export default usersReducer;
